"use client";

import Image from "next/image";
import { useRef } from "react";

export default function TiltImage({
  src,
  alt,
  width,
  height,
  maxTiltDeg = 14,
  glare = true,
  className = "",
}) {
  const wrapRef = useRef(null);

  function onMove(e) {
    const el = wrapRef.current;
    if (!el) return;

    const r = el.getBoundingClientRect();
    const x = e.clientX - r.left;
    const y = e.clientY - r.top;

    const px = x / r.width;   // 0..1
    const py = y / r.height;  // 0..1

    const rotY = (px - 0.5) * (maxTiltDeg * 2);
    const rotX = -(py - 0.5) * (maxTiltDeg * 2);

    el.style.setProperty("--rx", `${rotX}deg`);
    el.style.setProperty("--ry", `${rotY}deg`);

    // glare follows mouse
    el.style.setProperty("--gx", `${px * 100}%`);
    el.style.setProperty("--gy", `${py * 100}%`);
  }

  function onLeave() {
    const el = wrapRef.current;
    if (!el) return;
    el.style.setProperty("--rx", `0deg`);
    el.style.setProperty("--ry", `0deg`);
    el.style.setProperty("--gx", `50%`);
    el.style.setProperty("--gy", `50%`);
  }

  return (
    <div
      ref={wrapRef}
      onMouseMove={onMove}
      onMouseLeave={onLeave}
      className={`relative inline-block [perspective:900px] ${className}`}
      style={{ ["--rx"]: "0deg", ["--ry"]: "0deg", ["--gx"]: "50%", ["--gy"]: "50%" }}
    >
      <div
        className="relative rounded-2xl will-change-transform [transform-style:preserve-3d]
                   transition-transform duration-150"
        style={{
          transform: "rotateX(var(--rx)) rotateY(var(--ry)) translateZ(0px)",
        }}
      >
        <Image
          src={src}
          alt={alt}
          width={width}
          height={height}
          className="rounded-2xl select-none"
          draggable={false}
          priority={false}
        />

        {glare && (
          <div
            className="pointer-events-none absolute inset-0 rounded-2xl opacity-0 hover:opacity-100 transition-opacity duration-150"
            style={{
              background:
                "radial-gradient(280px 220px at var(--gx) var(--gy), rgba(255,255,255,0.28), rgba(255,255,255,0) 60%)",
              mixBlendMode: "screen",
            }}
          />
        )}
      </div>
    </div>
  );
}
